:root {
    --main-color: #ffc500
}

/* Margins and Paddings */
.top_60 {
    margin-top: 60px !important;
}

.top_30 {
    margin-top: 30px !important;
}

.p_30 {
    padding: 30px !important;
}

.p_50 {
    padding: 50px !important;
}

.pb_45 {
    padding-bottom: 40px !important;
}

.pt_37 {
    padding-top: 37px !important;
}

.mb_30 {
    margin-bottom: 30px !important;
}

@media (max-width: 991px) {
    .top_60 {
        margin-top: 30px !important;
    }
}



.container_shadow {
    box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.25);
    -webkit-box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.25);
}

.main-content {
    background-color: white;
    min-height: 100vh;
    border-radius: 6px;
    -mos-border-radius: 6px;
    -webkit-border-radius: 6px;
    margin-bottom: 30px;
}

.section_title {
    position: relative;
    display: inline-block;
    padding: 0 2px;
}

.section_title .section_title_text {
    font-size: 21px;
    font-weight: 600;
    position: relative;
    margin: 0;
}

.section_title span {
    position: absolute;
    height: 22px;
    width: 100%;
    bottom: 0;
    opacity: 0.4;
    left: 0;
    background-image: linear-gradient(white, var(--main-color));
}

.section {
    padding-left: 30px;
    padding-right: 30px;
}

.graybg {
    background-color: #fafafa;
}